<template>
    <div class="nav-bar">
        <div class="logo">
            <img src="https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1592713294872&di=3f6a5df941860c85e5d76dd0fd2c4f8c&imgtype=0&src=http%3A%2F%2Fphotocdn.sohu.com%2F20151231%2FImg433120571.jpg" />
        </div>
        <ul>
            <li v-for="(menu, index) in menuList" :key="index">
                <a-dropdown>
                    <!-- 一级菜单  标题 -->
                    <router-link :to="menu.path">
                        <a-icon :type="menu.ioc"/>
                        <span>{{menu.title}}</span>
                        <a-icon type="down" v-show="menu.sub.length > 0"/>
                    </router-link>
                    <!-- 二级菜单  下拉列表 -->
                    <a-menu slot="overlay" v-show="menu.sub.length > 0">
                        <!-- 二级菜单 有三级菜单的标题 -->
                        <a-sub-menu 
                            v-for="(sub, index) in menu.sub"
                            v-show="sub.sub.length > 0"
                            :key="'2-y-'+index">
                            <span slot="title">
                                <a-icon :type="sub.ioc"/>
                                <span>{{sub.title}}</span>
                            </span>
                            <!-- 三级菜单 三级菜单列表 -->
                            <a-menu-item
                                :class="currentIndex == index ? 
                                        'three-level-menu three-level-menu-active' : 'three-level-menu'"
                                v-for="(superSub, index) in sub.sub" 
                                :key="'3-'+index"
                                @click="clickThreeLevelMenu(
                                    {'ioc': superSub.ioc, 'index': superSub.id}
                                )">
                                <img width="18px" height="18px"
                                     v-show="suffixResolution(superSub.ioc)"
                                     style="border-radius: 3px" :src="superSub.ioc" />
                                <span>{{superSub.title}}</span>
                            </a-menu-item>
                            <!-- 三级菜单 三级菜单列表 -->
                        </a-sub-menu>
                        <!-- 二级菜单 有三级菜单的标题 -->
                        <!-- 二级菜单 没有三级菜单 -->
                        <a-menu-item 
                            v-show="sub.sub.length < 1"
                            v-for="(sub, index) in menu.sub" 
                            :key="'2-n-'+index">
                            <a-icon :type="sub.ioc"/>  
                            <span>{{sub.title}}</span>
                        </a-menu-item>
                        <!-- 二级菜单 没有三级菜单 -->
                    </a-menu>
                </a-dropdown>
            </li>
        </ul>
    </div>
</template>

<script>
export default {
    name: 'NavBar',
    props: {
        bgData: Array
    },
    data() {
        return{
            suffixList: ['png', 'jpg'],
            currentIndex: 0,
            menuList: [
            {"id": 1, "title": "首页", "path": "/", "ioc": "bank", sub: []},
            {"id": 2, "title": "标签", "path": "/tag", "ioc": "tag", sub: []},
            {"id": 3, "title": "分类", "path": "/classify", "ioc": "profile", sub: []},
            {"id": 4, "title": "留言", "path": "/", "ioc": "message", sub: []},
            {"id": 5, "title": "关于", "path": "/", "ioc": "apartment", sub: []},
            {"id": 6, "title": "登陆", "path": "/login", "ioc": "login", sub: []},
            {"id": 7, "title": "设置", "path": "/", "ioc": "setting", sub: [
                    {"id": 1, "title": "切换背景", "path": "/", "ioc": "bank", 
                        sub: this.bgData
                    },
                    {"id": 1, "title": "设置2", "path": "/", "ioc": "bank", sub: []},
                    {"id": 1, "title": "设置3", "path": "/", "ioc": "bank", sub: []},
                    {"id": 1, "title": "设置4", "path": "/", "ioc": "bank", sub: []},
                ]
            }
        ]
        }
    },
    methods: {
        clickThreeLevelMenu (ioc) {
            this.currentIndex = ioc.index;
            this.$emit('clickThreeLevelMenu', ioc);
        },
        suffixResolution (param) {
            let index = param.lastIndexOf(".") + 1;
            let suffix = param.substr(index, param.length)
            return this.suffixList.includes(suffix);
        }
    }
}
</script>

<style scoped>
    .nav-bar>.logo{
        margin-left: 20%;
        margin-top: .8%;
        float: left;
    }
    .nav-bar>.logo>img{
        width: 45px;
        height: 45px;
        border-radius: 5px;
    }
    .nav-bar>ul{
        float: left;
        margin-left: 5%;
        margin-top: 1%;
    }
    .nav-bar>ul>li{
        float: left ;
        width: 100px;
    }
    .nav-bar>ul>li>a{
        padding: 4px 8px;
        border-top: rgba(0, 0, 0, 0) solid 1px;
        border-bottom: rgba(0, 0, 0, 0) solid 1px;
        color: #108ee9;
        transition: .36s;
    }
    .nav-bar>ul>li>a:hover{
        border-color: #fff;
        color: #fff;
    }
</style>>


