import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'

import Home from '@/page/Home'
import FonrtHome from '@/page/fonrt/FonrtHome'
import TagBar from '@/page/fonrt/TagBar'
import ClassifyBar from '@/page/fonrt/ClassifyBar'
import Document from '@/page/fonrt/Document'
import Login from '@/page/fonrt/Login'

import ManageHome from '@/page/manager/ManageHome'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'FonrtHome',
      component: FonrtHome
    },
    {
      path: '/tag',
      name: 'TagBar',
      component: TagBar
    },
    {
      path: '/classify',
      name: 'ClassifyBar',
      component: ClassifyBar
    },
    {
      path: '/document',
      name: 'Document',
      component: Document
    },
    {
      path: '/login',
      name: 'Login',
      component: Login
    },
    {
      path: '/managerHome',
      name: 'ManageHome',
      component: ManageHome
    }
  ]
})



