<template>
  <div class="classify">
      <a-button-group class="classify-btn">
        <a-button 
            class="btn-item"
            @click="clickMode('left')">L</a-button>
        <a-button 
            class="btn-item"
            @click="clickMode('alternate')">M</a-button>
        <a-button 
            class="btn-item"
            @click="clickMode('right')">R</a-button>
      </a-button-group>

      <a-timeline :mode="mode" class="classify-time">
        <a-timeline-item 
            class="classify-item"
            v-for="(classify, index) in classifyList" :key="index">
            <a :href="classify.path">
                <span>{{classify.title}}</span>
                <span>{{classify.time}}</span>
            </a>
        </a-timeline-item>
    </a-timeline>
  </div>
</template>

<script>
export default {
    name: "classifyBar",
    data () {
        return{
            mode: "alternate",
            classifyList: [
                {"title": "JAVA", "path": "/", "time": "2020-09-09"},
                {"title": "SQL", "path": "/", "time": "2020-09-09"},
                {"title": "JAVA WEB", "path": "/", "time": "2020-09-09"},
                {"title": "MySQL", "path": "/", "time": "2020-09-09"},
                {"title": "Spring", "path": "/", "time": "2020-09-09"},
                {"title": "SpringMVC", "path": "/", "time": "2020-09-09"},
                {"title": "SpringBoot", "path": "/", "time": "2020-09-09"},
                {"title": "Liunx", "path": "/", "time": "2020-09-09"},
                {"title": "Nginx", "path": "/", "time": "2020-09-09"},
            ]
        }
    },
    methods: {
        clickMode(mode) {
            this.mode = mode;
        }
    }

}
</script>

<style scoped>
    .classify{
        width: 100%;
        margin-top: 4%;
    }
    .classify-btn{
        margin-left: 45%;
    }
    .classify-time{
        margin-top: 3%;
    }
</style>


