<template>
  <div class="tag-box">
      <div>
          <div class="tag-title">
              当前存在 <span id="tag-size-text">{{tagList.length}}</span> 个标签
          </div>
          <ul>
              <li class="tag-item" 
                  v-for="(tag, index) in tagList" 
                  :key="index">
                  <a href="#">
                        <div class="tag-title">{{tag.title}}</div>
                        <div class="tag-date">{{tag.time}}</div>
                  </a>
              </li>
          </ul>
      </div>
  </div>
</template>

<script>
export default {
    name: "TagBar",
    data () {
        return{
            tagList: [
                {"title": "JAVA", "path": "/", "time": "2020-09-09"},
                {"title": "SQL", "path": "/", "time": "2020-09-09"},
                {"title": "JAVA WEB", "path": "/", "time": "2020-09-09"},
                {"title": "MySQL", "path": "/", "time": "2020-09-09"},
                {"title": "Spring", "path": "/", "time": "2020-09-09"},
                {"title": "SpringMVC", "path": "/", "time": "2020-09-09"},
                {"title": "SpringBoot", "path": "/", "time": "2020-09-09"},
                {"title": "Liunx", "path": "/", "time": "2020-09-09"},
                {"title": "Nginx", "path": "/", "time": "2020-09-09"},
            ]
        }
    },
    mounted: function () {

        let tagItems = document.getElementsByClassName("tag-item");
        for(let tagItem of tagItems) {

            let tagLink = tagItem.firstChild.firstChild;
            let size = (Math.random() * 40) + 10;
            let r = Math.random() * 255;
            let g = Math.random() * 255;
            let b = Math.random() * 255;
            tagLink.style.fontSize = Math.round(size) + 'px';
            tagLink.style.color = 'rgb('+r+', '+g+', '+b+')';
            tagLink.style.borderColor = 'rgb('+r+', '+g+', '+b+')';
        }
        let r = Math.random() * 255;
        let g = Math.random() * 255;
        let b = Math.random() * 255;
        document.getElementById("tag-size-text").style.color = 'rgb('+r+', '+g+', '+b+')';
    },
    methods: {

    }
}
</script>

<style scoped>
    .tag-box {
        width: 80%;
        float: right;
        margin-right: 4%;
        margin-top: 12%;
    }
    .tag-item {
        margin-top: 2%;
        margin-left: 5%;
        padding: 2px 5px;
        float: left;
        text-align: center;
    }
    .tag-title, .tag-date{
        padding: 4px 10px;
        font-size: 20px;
    }
    .tag-date{
        font-size: 14px;
    }
</style>


