<template>
    <div class="home" :style="{backgroundImage: 'url('+defaultBg+')'}">
        <a-layout>
            <a-layout-header class="header">
                <NavBar :bgData="bgData" v-on:clickThreeLevelMenu="clickThreeLevelMenu"/>
            </a-layout-header>
            <a-layout>
                <a-layout-content class="main-content">
                    <router-view></router-view>
                </a-layout-content>
                <a-layout-sider>
                    <div class="back-top">
                        <a href="#">
                            <a-icon type="vertical-align-top" />
                            <span>{{scroll}}%</span>
                        </a>
                    </div>
                </a-layout-sider>
            </a-layout>
            <a-layout-footer>chanz</a-layout-footer>
        </a-layout>

    </div>
</template>

<script>
import NavBar from '@/components/nav/NavBar'
import FonrtHome from './fonrt/FonrtHome'
import ClassifyBar from './fonrt/ClassifyBar'
import Document from './fonrt/Document'
import Login from "./fonrt/Login"

import bg_1 from '@/assets/img/bg-1.jpg'
import bg_2 from '@/assets/img/bg-2.jpg'
import bg_3 from '@/assets/img/bg-3.jpg';

export default {
    name: "Home",
    data () {
        return {
            clientHeight: document.body.clientHeight,
            scroll: 0,
            bgData: [
                {"id": 0, "title": "背景-1", "path": "javascript:void(0);", "ioc": bg_1},
                {"id": 1, "title": "背景-2", "path": "javascript:void(0);", "ioc": bg_2},
                {"id": 2, "title": "背景-2", "path": "javascript:void(0);", "ioc": bg_3},
            ],
            defaultBg: bg_1
        }
    },
    components: {
        NavBar,
        FonrtHome,
        ClassifyBar,
        Document,
        Login
    },
    mounted () {
        window.addEventListener('scroll', this.handleScroll)
    },
    methods: {
        clickThreeLevelMenu: function (ioc) {
            this.defaultBg = ioc.ioc;
        },
        handleScroll(){
            let h = document.body.scrollHeight;
            this.clientHeight = document.documentElement.scrollTop||document.body.scrollTop
            this.scroll = Math.round(this.clientHeight / 10);
        }
    }
}
</script>
<style>
    @import "../assets/css/base.css";
    @import "../assets/css/color.css";
</style>
<style scoped>
    .home{
        position: absolute;
        width: 100%;
        /* background: url("../assets/img/bg-1.jpg"); */
        background-attachment: fixed;
        background-repeat: no-repeat;
        background-size: cover;
    }
    .header{
        width: 100%;
        height: 83px;
    }
    .ant-layout-has-sider{
        min-height: 800px;
    }
    .ant-layout-content{
        width: 84%;
    }
    .back-top{
        position: fixed;
        bottom: 14%;
        margin-left: 4%;
        padding: 3px 16px;
        cursor: pointer;
    }
</style>


