<template>
  <div class="login">
      <div class="login-box">

            <div class="login-switch">
                <span @click="switchLogin(true)">登陆</span>
                <span @click="switchLogin(false)">注册</span>
            </div>

            <!-- 登陆 -->
            <a-form 
                v-show="flag"
                :form="form">
                <a-form-item
                    label="账户"
                    class="login-time"
                    :label-col="{ span: 8 }"
                    :wrapper-col="{ span: 14 }">
                        <a-input 
                            placeholder="input user name">
                            <a-icon slot="prefix" type="user" style="color:rgba(0,0,0,.25)" />
                        </a-input>
                </a-form-item>
                <a-form-item
                    label="密码"
                    class="login-time"
                    :label-col="{ span: 8 }"
                    :wrapper-col="{ span: 14 }">
                        <a-input
                            type="password"
                            placeholder="input user password">
                            <a-icon slot="prefix" type="lock" style="color:rgba(0,0,0,.25)" />
                        </a-input>
                </a-form-item>
                <a-form-item :wrapper-col="{ span: 12, offset: 10 }">
                    <a-button @click="login" class="login-btn">
                        Submit
                    </a-button> 
                </a-form-item>
            </a-form>

            <!-- 注册 -->
            <a-form 
                v-show="!flag"
                :form="form">
                <a-form-item
                    label="账户"
                    class="login-time"
                    :label-col="{ span: 8 }"
                    :wrapper-col="{ span: 14 }">
                        <a-input 
                            placeholder="input user name">
                            <a-icon slot="prefix" type="user" style="color:rgba(0,0,0,.25)" />
                        </a-input>
                </a-form-item>
                <a-form-item
                    label="输入密码"
                    class="login-time"
                    :label-col="{ span: 8 }"
                    :wrapper-col="{ span: 14 }">
                        <a-input
                            type="password"
                            placeholder="input user password">
                            <a-icon slot="prefix" type="lock" style="color:rgba(0,0,0,.25)" />
                        </a-input>
                </a-form-item>
                 <a-form-item
                    label="确认密码"
                    class="login-time"
                    :label-col="{ span: 8 }"
                    :wrapper-col="{ span: 14 }">
                        <a-input
                            type="password"
                            placeholder="input user password">
                            <a-icon slot="prefix" type="lock" style="color:rgba(0,0,0,.25)" />
                        </a-input>
                </a-form-item>
                <a-form-item
                    label="性别"
                    :label-col="{ span: 8 }"
                    :wrapper-col="{ span: 14 }">
                    <a-select>
                        <a-select-option value="male">
                            male
                        </a-select-option>
                        <a-select-option value="female">
                            female
                        </a-select-option>
                    </a-select>
                </a-form-item>
                <a-form-item :wrapper-col="{ span: 12, offset: 10 }">
                    <a-button class="login-btn">
                        Submit
                    </a-button>
                </a-form-item>
            </a-form>
      </div>
      
  </div>
</template>

<script>

import ManageHome from "../manager/ManageHome";

export default {
    name: 'Login',
    data () {
        return{
            formLayout: 'horizontal',
            form: this.$form.createForm(this, { name: 'coordinated' }),
            flag: true,
        }
    },
    components: {
        ManageHome,
    },
    methods: {
        switchLogin(flag) {
            this.flag = flag;
        },
        login() {
            this.$router.push({path: '/managerHome'});
        }
    }
}
</script>

<style scoped>
    .login{
        width: 70%;
        margin-top: 10%;
        margin-left: 20%;
        padding: 10px;
    }
    .login-box{
        width: 70%;
        margin: 0 auto;
    }
    .login-switch{
        text-align: center;
        margin-bottom: 4%;
    }
    .login-switch>span{
        padding: 2px 6%;
        font-size: 20px;
        cursor: pointer;
    }
</style>


